import 'babel-polyfill';
import Vue from 'vue';
import VueRouter from 'vue-router';
import App from './App.vue';

const router = new VueRouter({
    mode: 'history',
    linkActiveClass: 'active',
    routes: [

    ]
});

Vue.use(VueRouter);

new Vue({
    el: '#app',
    components: {
        App
    },
    render: h => h('app'),
    router: router
});


